import React, { useState } from 'react';
import { Col, Row ,Image,Space, Switch, Card} from 'antd';

const { Meta } = Card;
export default function HomePage() {
	const [mode,setMode]=useState('day');
	const [bkcolor,setBkcolor]=useState('darkslategray');
	const [fontcolor,setFontcolor]=useState('white');
	const handleChangeMode=()=>{
		if(mode==='day'){
			setMode('night');
			setBkcolor('azure');
			setFontcolor('black');
		}else{
			setMode('day');
			setBkcolor('darkslategray');
			setFontcolor('white');
		}
	}
    return (
	<div style={{color:fontcolor,backgroundColor:bkcolor}}>
	<Row>
	<Col span={1}></Col>
	  <Col span={10}>
	  <h2>scriptLin!</h2>
	  <p></p>
	  </Col>
	  <Col span={9}>
	  <h4>github:  <a href="https://github.com/scriptLin-bjtu" target='_blank'>scriptLin</a></h4>
	  <h4>Email: p1091451463@hotmail.com</h4>
	  </Col>
	  <Col span={4}>
	  <Space direction="vertical" style={{marginTop:'2rem'}}>
	    <Switch checkedChildren="" unCheckedChildren="" defaultChecked onChange={handleChangeMode}/>
	  </Space>
	  </Col>
	</Row>
	
	<div style={{height:"2rem"}}></div>
	<Row>
	<Col span={1}></Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<img alt="pm" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} />}
	   >
	     <Meta title="PuppetMaster " description="electron+opencv+robotjs " />
	   </Card>
	 </Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<><img alt="example" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} /></>}
	   >
	     <Meta title="" description="react" />
	   </Card>
	 </Col>
	 <Col span={1}></Col>
	</Row>
	
	<div style={{height:"2rem"}}></div>
	<Row>
	<Col span={1}></Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<img alt="flappybird" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} />}
	   >
	     <Meta title="FlappyBird " description="'FlappyBird' " />
	   </Card>
	 </Col>
	 <Col span={11}>
	 <Card
	     hoverable
	     style={{
	       width: "75%"
	     }}
	     cover={<><img  alt="example" src={process.env.PUBLIC_URL+'/assets/puppetmaster.png'} /></>}
	   >
	     <Meta title="VideoStamp " description="edge" />
	   </Card>
	 </Col>
	 <Col span={1}></Col>
	</Row>
	</div>
	);
}
